export default {
	data() {
		return {
			page: 1, //
			limit: 10, //
			loadingType: 1, //0 1 2
			isLoading: false, //
			loaded: false, //
			queryUrl: '',
			queryData: {},
			dataList: [],
		}
	},
	
	methods: {
		navTo(url, type, checkLogin = false) {
			this.$util.throttle(() => {
				if (!url) {
					return;
				}
				if (url == 'back') {
					uni.navigateBack()
					return
				}
				// 
				if (checkLogin) {
					uni.showToast({
						title: '',
						icon: 'none'
					})
					return
				}
				if (type == 'switch') {
					uni.switchTab({
						url: url
					})
				} else {
					uni.navigateTo({
						url: url
					})
				}

			}, 300)
		},
		getList() {
			return new Promise((resolve,reject)=>{
				let data = {
					page: this.page,
					limit: this.limit,
					...this.queryData
				}
				this.$http(this.queryUrl, data).then(res => {
					if(!res) return;
					this.dataList = this.dataList.concat(Array.isArray(res)?res:  (res.list || res.data || [])); 
					resolve(this.dataList)
					// 
					this.isRequestState = Number(res.total) <= this.dataList.length ? true : false;
					
					
				})
			})
		},
	},
	// 
	onReachBottom(){
		if(this.isContinue){
			this.queryData.current++
			this.getData() || this.getList()
		}
	},
}
